import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { register, resetError, resetRegistrationSuccess } from '../store/authSlice';

const registerSchema = Yup.object().shape({
  username: Yup.string()
    .min(3, 'Username must be at least 3 characters')
    .max(20, 'Username must be less than 20 characters')
    .required('Username is required'),
  email: Yup.string()
    .email('Invalid email format')
    .required('Email is required'),
  password: Yup.string()
    .min(6, 'Password must be at least 6 characters')
    .required('Password is required'),
  confirmPassword: Yup.string()
    .oneOf([Yup.ref('password'), null], 'Passwords must match')
    .required('Confirm password is required'),
});

const Register = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { isLoading, error, registrationSuccess, isAuthenticated } = useSelector((state) => state.auth);

  useEffect(() => {
    // Clear any previous errors and registration success
    dispatch(resetError());
    dispatch(resetRegistrationSuccess());
  }, [dispatch]);

  useEffect(() => {
    // Redirect if already authenticated
    if (isAuthenticated) {
      navigate('/');
    }
  }, [isAuthenticated, navigate]);

  useEffect(() => {
    // Redirect to login after successful registration
    if (registrationSuccess) {
      navigate('/login');
    }
  }, [registrationSuccess, navigate]);

  const handleSubmit = (values, { resetForm }) => {
    // Remove confirmPassword before sending to API
    const { confirmPassword, ...userData } = values;
    dispatch(register(userData));
  };

  return (
    <div className="flex justify-center items-center min-h-[80vh]">
      <div className="w-full max-w-md p-8 card">
        <h1 className="text-2xl font-bold text-center mb-6">Register for Puente Trading</h1>
        
        {error && (
          <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
            {error}
          </div>
        )}
        
        <Formik
          initialValues={{ username: '', email: '', password: '', confirmPassword: '' }}
          validationSchema={registerSchema}
          onSubmit={handleSubmit}
        >
          {({ isSubmitting }) => (
            <Form>
              <div className="mb-4">
                <label htmlFor="username" className="label">Username</label>
                <Field
                  type="text"
                  id="username"
                  name="username"
                  className="input"
                  placeholder="Choose a username"
                />
                <ErrorMessage name="username" component="div" className="error" />
              </div>
              
              <div className="mb-4">
                <label htmlFor="email" className="label">Email</label>
                <Field
                  type="email"
                  id="email"
                  name="email"
                  className="input"
                  placeholder="Enter your email"
                />
                <ErrorMessage name="email" component="div" className="error" />
              </div>
              
              <div className="mb-4">
                <label htmlFor="password" className="label">Password</label>
                <Field
                  type="password"
                  id="password"
                  name="password"
                  className="input"
                  placeholder="Create a password"
                />
                <ErrorMessage name="password" component="div" className="error" />
              </div>
              
              <div className="mb-6">
                <label htmlFor="confirmPassword" className="label">Confirm Password</label>
                <Field
                  type="password"
                  id="confirmPassword"
                  name="confirmPassword"
                  className="input"
                  placeholder="Confirm your password"
                />
                <ErrorMessage name="confirmPassword" component="div" className="error" />
              </div>
              
              <button
                type="submit"
                disabled={isLoading || isSubmitting}
                className="btn btn-primary w-full"
              >
                {isLoading ? 'Registering...' : 'Register'}
              </button>
            </Form>
          )}
        </Formik>
        
        <div className="mt-4 text-center">
          <p>
            Already have an account?{' '}
            <Link to="/login" className="text-blue-600 hover:underline">
              Login here
            </Link>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Register; 